import { POTUserModel } from '@appModels/POT.user.model';
import { POTRouterService } from '@appServices/POT.router.service';
import { POTUserService } from '@appServices/POT.user.service';
import { POTAppConfig } from '@appInyectables/POT.app.config';
import { Component, OnInit, OnDestroy } from '@angular/core';
import { POTStorageService } from '@appServices/POT.storage.service';
import { Observable } from 'rxjs';

/**
 * Shows user's data
 */
@Component({
  selector: 'app-user-detail',
  templateUrl: './user-detail.component.html',
  styleUrls: ['./user-detail.component.scss']
})
// TODO: Refactor all this component
export class UserDetailComponent implements OnInit, OnDestroy {
  private loggedUser$: Observable<POTUserModel>;
  private userActive: POTUserModel;
  /**
 * @ignore
 */
  protected log = POTAppConfig.logFactory.getLogger('log.component.user-detail');
  constructor(private storageService: POTStorageService, private userService: POTUserService, private routerService: POTRouterService) {
    this.loggedUser$ = this.storageService.loggedUser;
  }
  /**
   * @ignore
   */
  ngOnInit() {
    let loggedUser: POTUserModel;
    this.storageService.loggedUser.subscribe((value) => {
      loggedUser = value;
    });
    const userOnURL = this.routerService.currentURL.split('/users/')[1];
    if (loggedUser.username === userOnURL) {
      this.log.debug('user-detail on loggedUser');
      this.userActive = loggedUser;
      console.log(this.userActive); // TODO: Remove this
    } else {
      this.log.debug('user-detail on NOT loggedUser');
      this.userService.getUser(userOnURL).then((value) => {
        this.userActive = value;
        console.log(this.userActive); // TODO: Remove this
      });
    }
  }
  /**
   * @ignore
   */
  ngOnDestroy() {
  }
}
